
 .prices {
  position: relative;
  width: 100%;
  height: 100vh;
  background: radial-gradient(#c6ece6,#fff);
  display: flex;
  align-items: center;
  justify-content: center;
}

 .set {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;

    img {
      max-width: 100px;
      height: 100px;
      z-index: 0;

      @include for-phone {
        max-width: 65px;
        height: 65px;
    }
  }
}
 .set div {
  position: absolute;
  display: block;
}
 .set div:nth-child(1) {
  left: 20%;
  animation: animate 15s linear infinite;
  animation-delay: -7s;
}
 .set div:nth-child(2) {
  left: 50%;
  animation: animate 20s linear infinite;
  animation-delay: -5s;
}
 .set div:nth-child(3) {
  left: 70%;
  animation: animate 20s linear infinite;
  animation-delay: 0s;
}
 .set div:nth-child(4) {
  left: 20%;
  animation: animate 15s linear infinite;
  animation-delay: -5s;
}
 .set div:nth-child(5) {
  left: 85%;
  animation: animate 18s linear infinite;
  animation-delay: -10s;
}
 .set div:nth-child(6) {
  left: 0%;
  animation: animate 12s linear infinite;
}
 .set div:nth-child(7) {
  left: 15%;
  animation: animate 14s linear infinite;
}
 .set div:nth-child(8) {
  left: 60%;
  animation: animate 15s linear infinite;
}

@keyframes animate {
  0% {
    opacity: 0;
    top: -10%;
    transform: translateX(20px) rotate(0deg);
  }
  10% {
    opacity: 1;
  }
  20% {
    transform: translateX(-20px) rotate(45deg);
  }
  40% {
    transform: translateX(-20px) rotate(90deg);
  }
  60% {

    transform: translateX(20px) rotate(180deg);
  }
  80% {
    transform: translateX(20px) rotate(180deg);
  }
  100% {
    top: 110%;
    transform: translateX(020px) rotate(225deg);
  }
}
.set2 {
  transform: scale(2) rotateY(180deg);
  filter: blur(2px);
}
.set3 {
  transform: scale(0.8) rotateX(180deg);
  filter: blur(6px);
}

.price-content {

  .price-item {
    background: #ffffff99;
    border-radius: 15px;
    margin: 10px;
  }

  p {
    padding: 15px 25px;
    background: none;
    margin: 0;

    @include for-phone {
       padding: 8px 12px;
    }
  }
  strong {
    font-size: 22px;
    text-align: center;

    @include for-phone {
        font-size: 16px;
    }
  }
}